import { useEffect, useRef, useState } from 'react';
import { HStack } from 'rsuite';
import './App.css';

function App() {
  const [stream, setStream] = useState<MediaStream | null>();
  const [imageSourceDataList, setImageSourceDataList] = useState<string[]>([]);

  const canvasRef = useRef<HTMLCanvasElement>(null);
  const videoRef = useRef<HTMLVideoElement>(null);

  const [isActive, setIsActive] = useState(false);
  const timerRef = useRef<NodeJS.Timeout | null>(null);

  useEffect(() => {
    if (!stream) return;
    if (!videoRef?.current) return;

    videoRef.current.srcObject = stream;
    videoRef.current.play();
  }, [videoRef, stream]);

  const handleHideShowClick = () => {
    if (!stream) {
      createStream();
    } else {
      destroyStream();
    }
  };

  function captureStream() {
    if (!videoRef?.current) return;
    if (!canvasRef?.current) return;

    const context = canvasRef.current.getContext("2d");
    if (!context) return;

    const { videoWidth, videoHeight } = videoRef.current;

    canvasRef.current.width = videoWidth;
    canvasRef.current.height = videoHeight;

    context.drawImage(videoRef.current, 0, 0, videoWidth, videoHeight);

    const data = canvasRef.current.toDataURL("image/png");
    setImageSourceDataList((prevList) => [...prevList, data]);

    // Convert canvas to Blob and send to server
    // canvasRef.current.toBlob(async (blob) => {
    //   if (blob === null) return;

    //   const formData = new FormData();
    //   formData.append('file', blob, 'capture.png');

    //   try {
    //     const response = await fetch('http://127.0.0.1:8000/upload/', {
    //       method: 'POST',
    //       body: formData,
    //     });

    //     if (response.ok) {
    //       const data = await response.json();
    //       alert(data.message);
    //     } else {
    //       alert('Failed to upload photo');
    //     }
    //   } catch (error) {
    //     console.error('Error uploading photo:', error);
    //     alert('An error occurred while uploading the photo');
    //   }
    // });
  };

  async function createStream() {
    const videoStream = await navigator.mediaDevices.getUserMedia({
      audio: false,
      video: {
        facingMode: 'environment'
      }
    });
    setStream(videoStream);
  };

  function destroyStream() {
    if (!stream) return;

    stream.getTracks().forEach((track) => {
      if (track.readyState === 'live' && track.kind === 'video') {
        track.stop();
      }
    });
    setStream(null);
  };

  const startTimer = () => {
    if (!timerRef.current) {
      timerRef.current = setInterval(captureStream, 1000);
    }
  };

  const stopTimer = () => {
    if (timerRef.current) {
      clearInterval(timerRef.current);
      timerRef.current = null;
    }
  };

  const handleDown = () => {
    startTimer();
  };

  const handleUp = () => {
    stopTimer();
  };

  function simpleHash(input: string, mod: number = 100000): number {
    let hash = 0;
    for (let i = 0; i < input.length; i++) {
      const charCode = input.charCodeAt(i);
      hash = (hash * 31 + charCode) % mod;  // 31 is a commonly used prime multiplier
    }
    return hash;
  }

  const imgs = imageSourceDataList.map(imageSourceData => <img key={simpleHash(imageSourceData)} src={imageSourceData} width="250" height="250" />)

  return (
    <div>
      <button onClick={handleHideShowClick}>{stream ? "hide camera" : "show camera"}</button>
      <HStack>
        <div onMouseDown={handleDown} onMouseUp={handleUp} onTouchStart={handleDown} onTouchEnd={handleUp}>
          <video hidden={!stream} ref={videoRef} />
          <canvas hidden ref={canvasRef} />
          {/* <img hidden={!!stream || !imageSourceDataList} ref={imageRef} alt="The screen capture will appear in this box." /> */}
        </div>
        {imgs}
      </HStack>
    </div>
  );
}

export default App;
